.footer {
    padding: 0 10% 20px 10%;
    margin-top: 20px;
    font-family: Roboto, sans-serif;
    z-index: 1;
    background: #fff;

    @media screen and (min-height: 900px) {
        margin-top: 0;
        box-shadow: 0 -40px 80px #fffd;
    }

    .topSection {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 30px;
        border-top: 1px solid #e4eaf2;

        .logo {
            width: 20%;

            @media screen and (max-width: 800px) {
                display: none;
            }

            img {
                width: 100px;
            }
        }

        .links {
            display: flex;
            flex: 1;
            margin: 0 20px;

            div {
                flex: 1;
            }
        }

        .links h4 {
            margin: 6px 0 12px 0;
            font-size: 16px;
            color: #455363;
        }

        .links ul {
            list-style: none;
            padding: 0;
        }

        .links li {
            font-size: 14px;
            line-height: 1.8;
            color: #455363;
            cursor: pointer;

            &:hover {
                color: #49cc68;
            }
        }

        .appLinks {
            display: flex;
            gap: 20px;

            img {
                width: 20px;
            }
        }

    }

    .bottomSection {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 20px 0;

        .bottomText {
            text-align: left;
            font-size: 12px;
            color: #7b8c9f99;
        }

        p {
            margin: 5px 0;
            line-height: 1.5;
        }

        .qrCode {
            width: 40px;
        }
    }
}